
footer{
  background-color: var(--color-dark-blue-gray);
  background-image: -webkit-gradient(linear, left top, left bottom, from(hsla(0, 0%, 0%, 0.1)), to(hsla(0, 0%, 0%, 0.4)));
  background-image: linear-gradient(hsla(0, 0%, 0%, 0.1), hsla(0, 0%, 0%, 0.4));
  min-height: 100px;
  position: relative;
  overflow: hidden;
  padding: 20px 0;
}
footer>.container{
  position: relative;
  z-index: 1;
}
footer::before{
  content: "";
  position: absolute;
  -webkit-box-shadow: 0 1px 2px black, 0 0px 50px hsla(0, 0%, 0%, 0.9);
          box-shadow: 0 1px 2px black, 0 0px 50px hsla(0, 0%, 0%, 0.9);
  height: 50px;
  left: 0;
  top: -50px;
  width: 100%;
  -webkit-animation: slowUpDown 1.8s infinite both alternate ease;
          animation: slowUpDown 1.8s infinite both alternate ease;
}
li{
  font-size: 18px;
  font-weight: 300;
  line-height: 1.4;
}
@-webkit-keyframes slowUpDown{
from{
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px)
}
to{
    -webkit-transform: translateY(0px);
            transform: translateY(0px)
}
}
@keyframes slowUpDown{
from{
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px)
}
to{
    -webkit-transform: translateY(0px);
            transform: translateY(0px)
}
}



