
.items{
  width: 100vw;
  height: 100vh;
  text-align: center;
  background-image: url('../assets/image-2.png');
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.item{
  height: 80px;
  width: 100%;
  border-radius: 6px;
  background: var(--color-light);
  -webkit-box-shadow:  0 1px 2px hsla(0, 0%, 0%, 0.2), 0 6px 18px hsla(0, 0%, 0%, 0.2);
          box-shadow:  0 1px 2px hsla(0, 0%, 0%, 0.2), 0 6px 18px hsla(0, 0%, 0%, 0.2);
}
.mar-top-md{
  margin-top: 60px;
}




