
header{
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  padding: 15px;
  background: hsla(0, 0%, 0%, 0.2);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
nav>a{
  color: var(--color-light);
  text-decoration: none;
  font-size: 22px;
  margin-left: 20px;
  opacity: 0.85;
}
nav>a.primary{
  font-weight: bold;
  opacity: 0.9;
  margin-left: 30px;
}
nav>a:hover{
  opacity: 1;
}



